@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;700&display=swap');

.App {
  text-align: center;
}

.Player-container {
  width: 90%;
  max-height: 40vh;
  position: relative;
  padding-bottom: 50.625%;
  margin-left: 5%;
  margin-right: 5%;
  margin-top: 16px;
}

iframe.Player {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.Link-phone {
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 8px;
  padding-right: 8px;
  margin-top: 10px;
  margin-right: 12px;
  background-color: #0EBE2C;
  font-weight: 700;
  border-radius: 10px;
  display: flex;
  align-items: center;
}

.Link-phone:link {
  color: #fafafa;
  text-decoration: none;
}

.Link-phone:visited {
  color: #fafafa;
  text-decoration: none;
}

.Link-phone:hover {
  color: #8aff9e;
  text-decoration: none;
}

.Link-phone:active {
  color: #8aff9e;
  text-decoration: none;
}

.Link-kakao {
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 8px;
  padding-right: 8px;
  margin-top: 10px;
  background-color: #ffe812;
  font-weight: 700;
  border-radius: 10px;
  display: flex;
  align-items: center;
}

.Link-kakao:link {
  color: #3F3035;
  text-decoration: none;
}

.Link-kakao:visited {
  color: #3F3035;
  text-decoration: none;
}

.Link-kakao:hover {
  color: #938563;
  text-decoration: none;
}

.Link-kakao:active {
  color: #938563;
  text-decoration: none;
}

.Section-container {
  margin-bottom: 24px;
}

.Section-title-container {
  background: #002E5D;
  width: 100%;
  padding-top: 12px;
  padding-bottom: 12px;
}

.Section-body-container {
  max-width: 1000px;
  margin: auto;
}

.Section-title-text {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 26px;
  font-weight: 700;
  color: white;
  height: 36px;
}

.Section-single-paragraph {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: flex-start;
  margin-bottom: 32px;
}

.Section-single-title {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 8px;
}

.Section-single-text {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-align: start;
}

.Section-single-spacer {
  height: 12px;
}

.Section-split-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.Section-center-content {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Section-image {
  height: 90%;
  max-height: 335px;
}

.Section-split-box {
  width: 42.5%;
  margin-top: 16px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  margin-left: 5%;
  margin-right: 5%;
}

.Map-container {
  width: 42.5%;
  min-height: 20vh;
  position: relative;
  margin-left: 5%;
  margin-top: 16px;
}

iframe.Map {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (max-width : 669px) {
  .Section-split-container {
    flex-direction: column;
  }

  .Section-split-box {
    width: 90%;
    margin-left: 5%;
  }

  .Section-image {
    max-height: 22vh;
    margin-bottom: 12px;
  }

  .Map-container {
    width: 90%;
    min-height: 28vh;
  }
}

a:link {
  color: #002E5D;
  text-decoration: none;
}

a:visited {
  color: #002E5D;
  text-decoration: none;
}

a:hover {
  color: rgb(20, 116, 137);
  text-decoration: none;
}

a:active {
  color: rgb(20, 116, 137);
  text-decoration: none;
}