.App-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: calc(10px + 2vmin);
    color: black;
    position: relative;
}

.Header-background {
    background-image: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8)), url("assets/header.webp");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    filter: blur(4px);
    -webkit-filter: blur(4px);
    height: fit-content;
    position: absolute;
    left: 0;
    right: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 100%;
}

.Header-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    width: 70%;
    margin-top: 40px;
    margin-bottom: 36px;
}

.Header-divider {
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 16px;
    font-weight: 400;
    color: #bdbdbd;
    margin-left: 20px;
    margin-right: 20px;
}

.Header-text {
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 16px;
    font-weight: 400;
}

.Logo-img {
    width: 200px;
}

.Logo-text {
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 36px;
    font-weight: 700;
    color: #002E5D;
    text-shadow: #fff -1px -1px 0px, #fff -1px 1px 0px, #fff 1px 1px 0px, #fff 1px -1px 0px;
}

.Logo-text-2 {
    font-family: 'Noto Sans KR', sans-serif;
    font-weight: 400;
    font-size: 20px;
    color: rgb(20, 116, 137);
}

.Logo-text-3 {
    margin-top: 8px;
    font-family: 'Noto Sans KR', sans-serif;
    font-weight: 400;
    font-size: 16px;
    margin-bottom: 72px;
    color: #401E00;
}

@media (max-width : 669px) {
    .Header-container {
        flex-direction: column;
    }

    .Header-divider {
        display: none;
    }

    .Logo-img {
        width: 160px;
    }

    .Logo-text {
        font-size: 32px;
    }

    .Logo-text-2 {
        font-size: 18px;
    }

    .Logo-text-3 {
        font-size: 16px;
    }
}